import React from 'react';

import './PainelJogador.css';

const PainelJogador = ({jogadorId, onClick, selecionado}) => {

    return (
        <div
            onClick={() => onClick(jogadorId)}
            className={`painel-jogador${(selecionado && '-selecionado') || ''} jogador-${jogadorId}`} />
    )
}

export default PainelJogador;