import React, { useEffect, useState } from 'react';
import Roleta from './Roleta';
import PainelJogador from './PainelJogador';


import siren from './sounds/siren.mp3';


import { randomInt } from 'mathjs';
import './App.css';
import './Jogadores.css';

function App() {

  const [count, setCount] = useState(0); 
  const [countMax, setCountMax] = useState(0); 

  const [selecionados, setSelecionados] = useState([]); 

  const [alarmeOn, setAlarmeOn] = useState(0);

  const [corMoldura, setCorMoldura] = useState('#9b9fa1');
  
  const sirene = new Audio(siren);

  useEffect(() => {
    if(alarmeOn > 0) {
      setTimeout(() => { setAlarmeOn(alarmeOn - 1)}, 700);
    }
  }, [alarmeOn])
  
  sirene.load();

  const jogadores = [
    'alessandro',
    'alexandre',
    'arthur',
    'bonke',
    'carol',
    'coletto',
    'diogo',
    'edu',
    'fabricio',
    'guilherme',
    'guto',
    'igor',
    'liandra',
    'mariana',
    'romao',
    'yago',
  ];
  
  const startRoleta = () => {
    setCorMoldura('#9b9fa1');
    const randonChoice = randomInt(0, jogadores.length);
    const newCountMax = 50 - jogadores.length + randonChoice;
    setCount(0);
    setCountMax(newCountMax);
  }

  const selecionaJogador = (idJogador) => {
    if(selecionados.includes(idJogador)) setSelecionados(selecionados.filter(v => v!== idJogador));
    else setSelecionados([...selecionados, idJogador].sort())
  }

  const handleFimRoleta = () => {    
    setCorMoldura('#F80000');
    sirene.play();
    setAlarmeOn(24);
  }


  return (
    <div className={`App ${alarmeOn % 2 === 1 && 'alarme-ligado'}`}>
      <div className="content-wrapper">
      <div className="janela-wrapper" >      
        <figure class="astronauta"><span class="shadow"></span></figure>
      </div>
      <Roleta
        count={count}
        updateCount={(newCount) => setCount(newCount)}
        countMax={countMax} jogadores={jogadores.filter(j => selecionados.includes(j))}
        disparaAlarme={handleFimRoleta}
        corMoldura={corMoldura}
      />
      <div className="painel-jogadores" >
      <div className="painel-jogadores-imagens" >
        {jogadores.map(j => {
          return (
            <PainelJogador
              jogadorId={j}
              onClick={selecionaJogador}
              selecionado={selecionados.includes(j)}  
            />
          )
        })}
        </div>
      <div className="painel-botao" onClick={startRoleta}>RODAR</div>
      </div>
      
      </div>
      <figure class="bola"><span class="shadow"></span></figure>
    </div>
  );
}

export default App;
