import React, { useEffect, useState } from 'react';
import boop from './sounds/boop.mp3';

import siren from './sounds/siren.mp3';
import './Roleta.css';

const Roleta = ({count, updateCount, countMax, jogadores, disparaAlarme, corMoldura}) => {
    
  const [escolhido, setEscolhido] = useState(0);
    
    
  const audio = new Audio(boop);
  const sirene = new Audio(siren);

  audio.load();
  sirene.load();


  let atual = jogadores[escolhido] ? escolhido : 0;    
  let anterior = atual === 0 ? jogadores.length - 1 : atual - 1;
  let posterior =  atual === jogadores.length - 1 ? 0 : atual + 1;  
      const renderNext = () => {
        audio.play();
        const proximo = atual + 1;
        if(proximo === jogadores.length) {
          setEscolhido(0);
        }
        else {
          setEscolhido(proximo);
        }
        updateCount(count+1);
      }
    
      useEffect(() => {
        const multi = count > 0.9*countMax ? 20 * (count - 0.9*countMax) : 10;
        if(count < countMax){
            setTimeout(() => { renderNext();}, multi * count);
            
        }
        else if (count >= countMax && count > 0) {
          disparaAlarme();
        }
      }, [countMax, escolhido]);
      

      if(jogadores.length === 0) {
        return(
          <div className="roleta-wrapper">
          <div className="moldura" style={{ borderColor: corMoldura }}/>
          <div className="fundo"/>
            <div className="roleta">
              <div className="jogador-wrapper jogador-anterior jogador-unkown"/>
              <div className="jogador-wrapper jogador-atual jogador-unkown"/>
              <div className="jogador-wrapper jogador-posterior jogador-unkown"/>
            </div>
          </div>
        );
      }

    

    if(jogadores.length === 1) {
      anterior = 0;
      posterior = 0;
    }

    return (
    <div className="roleta-wrapper">    
      <div className="moldura" style={{ borderColor: corMoldura }}/>      
      <div className="fundo"/>
        <div className="roleta">
            <div className={`jogador-wrapper jogador-anterior jogador-${jogadores[anterior]}`} />
            <div className={`jogador-wrapper jogador-atual jogador-${jogadores[atual]}`} />
            <div className={`jogador-wrapper jogador-posterior jogador-${jogadores[posterior]}`} />
        </div>
    </div>
    );
}

export default Roleta;